import React, { useState, useEffect } from 'react';
import './App.css';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';


let styles = {
  margin: 'auto',
  width: '500px'
};

const Carsl = () => {
  const [images, setImages] = useState([]);

  const fetchImagesSingle = async () => {
    let res = []
    const width = 500
    const height = 400
    for (let count = 20; count > 0; count--) {
      res.push(fetch(`https://picsum.photos/${width}/${height}`));
    }
    const data = Promise.all(res).then(values => {
      let counter = 1;
      return values.map(item => ({download_url: item.url, author: `Image: ${counter++}`}))
    })
    return data
  }

  const fetchImagesList = async () => {
    const res = await fetch('https://picsum.photos/v2/list?page=17&limit=20')
    const data = await res.json()
    return data
  }

  useEffect(() => {
    const getImages = async () => {
      const images = await fetchImagesSingle();
      setImages(images);
    }
    getImages()
  }, [])

  return (
    <div style={styles}>
      <Carousel
        showArrows={true}
        useKeyboardArrows={true}
        infiniteLoop={true}
        emulateTouch={true}
        autoPlay={true}
        onClickItem={(item) => console.log(item)}>
        {images.map(
          entry => (
            <div key={entry.author}>
              <img src={entry.download_url} alt={entry.author} />
              <p className="legend">{entry.author}</p>
            </div>
          )
        )
        }
      </Carousel>
    </div>
  )
}

export default Carsl;